@font-face {
  font-family: 'RobotoMono';
  src: url(../fonts/RobotoMono-Thin.ttf)
}

* {
  font-family: 'RobotoMono', monospace;
  overflow: hidden;
}

body {
  -ms-flex-align: center;
      align-items: center;
  background: -webkit-gradient(linear, left top, right top, from(rgb(83, 105, 118)), to(rgb(41, 46, 73)));
  background: -webkit-linear-gradient(left, rgb(83, 105, 118), rgb(41, 46, 73));
  background: -o-linear-gradient(left, rgb(83, 105, 118), rgb(41, 46, 73));
  background: linear-gradient(to right, rgb(83, 105, 118), rgb(41, 46, 73));
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  height: 100vh;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}



